import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_DELETE_COLS = Symbol('TABLE_DELETE_COLS');

export function deleteTableCols(table) {
  return {
    type: TABLE_DELETE_COLS,
    payload: {
      node: table,
    },
  };
}

interface TableDeleteColsPayload {
  node: Table;
}

interface TableDeleteColsAction {
  type: typeof TABLE_DELETE_COLS;
  payload: TableDeleteColsPayload;
}

export function isTableDeleteColsAction(
  action: Action,
): action is TableDeleteColsAction {
  return action.type === TABLE_DELETE_COLS;
}